<template>
  <div>
    <el-dialog :title="info.title" :visible.sync="info.isshow" @opened="opened">
      <el-form :model="form">
        <el-form-item label="一级分类" label-width="120px">
          <el-select v-model="form.first_cateid" @change="changeFirstid">
            <el-option label="--请选择--" value disabled></el-option>
            <el-option
              v-for="item in classifyList"
              :key="item.id"
              :label="item.catename"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="二级分类" label-width="120px">
          <el-select v-model="form.second_cateid">
            <el-option label="--请选择--" value disabled></el-option>
            <el-option
              v-for="item in secondClassifyList"
              :key="item.id"
              :label="item.catename"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="商品名称" label-width="120px">
          <el-input v-model="form.goodsname" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="价格" label-width="120px">
          <el-input v-model="form.price" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="市场价格" label-width="120px">
          <el-input v-model="form.market_price" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="图片" label-width="120px" v-if="form.pid!=0">
          <div class="my-upload">
            <h3>+</h3>
            <img v-if="imgUrl" :src="imgUrl" alt />

            <input v-if="info.isshow" type="file" class="ipt" @change="changeFile" />
          </div>
        </el-form-item>
        <el-form-item label="商品规格" label-width="120px">
          <el-select v-model="form.specsid" @change="changeSpecs">
            <el-option label="--请选择--" value disabled></el-option>
            <el-option
              v-for="item in spcesList"
              :key="item.id"
              :label="item.specsname"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="规格属性" label-width="120px">
          <el-select v-model="form.specsattr" multiple>
            <el-option label="--请选择--" value disabled></el-option>
            <el-option v-for="item in spcesAttrs" :key="item" :label="item" :value="item"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="是否新品" label-width="120px">
          <el-radio v-model="form.isnew" :label="1">是</el-radio>
          <el-radio v-model="form.isnew" :label="2">否</el-radio>
        </el-form-item>
        <el-form-item label="是否热卖" label-width="120px">
          <el-radio v-model="form.ishot" :label="1">是</el-radio>
          <el-radio v-model="form.ishot" :label="2">否</el-radio>
        </el-form-item>
        <el-form-item label="状态" label-width="120px">
          <el-switch v-model="form.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
        <el-form-item label="商品描述" label-width="120px">
          <div v-if="info.isshow" id="editor"></div>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="add" v-if="info.title=='商品管理添加'">添 加</el-button>
        <el-button type="primary" @click="update" v-else>修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import E from "wangeditor";
import { mapActions, mapGetters } from "vuex";
import {
  reqClassifyList,
  reqGoodsAdd,
  reqGoodsListOne,
  reqGoodsUpdate
} from "../../../util/request";
import { alertSuccess, alertWarning } from "../../../util/alert";
export default {
  props: ["info"],
  computed: {
    ...mapGetters({
      classifyList: "classify/classify",
      spcesList: "spces/spces"
    })
  },
  components: {},
  data() {
    return {
      form: {
        first_cateid: "",
        second_cateid: "",
        goodsname: "",
        price: "",
        market_price: "",
        img: null,
        description: "",
        specsid: "",
        specsattr: [],
        isnew: 1,
        ishot: 1,
        status: 1
      },
      secondClassifyList: [],
      spcesAttrs: [],
      imgUrl: ""
    };
  },
  methods: {
    opened() {
      this.editor = new E("#editor");
      this.editor.create();
      this.editor.txt.html(this.form.description);
    },
    empty() {
      this.form = {
        first_cateid: "",
        second_cateid: "",
        goodsname: "",
        price: "",
        market_price: "",
        img: null,
        description: "",
        specsid: "",
        specsattr: [],
        isnew: 1,
        ishot: 1,
        status: 1
      };
      this.secondClassifyList = [];
      this.spcesAttrs = [];
      this.imgUrl = "";
    },
    ...mapActions({
      requsetClassifyList: "classify/requsetClassifyList",
      requsetSpcesList: "spces/requsetSpcesList",
      requsetGoodsList: "goods/requsetGoodsList",
      requsetGoodsConut: "goods/requsetGoodsConut"
    }),
    changeFile(e) {
      let file = e.target.files[0];
      if (file.size > 2 * 1024 * 1024) {
        alertWarning("文件不能超过2M");
        return;
      }
      let imgArrs = [".jpg", ".png", ".gif", ".jpeg"];
      if (!imgArrs.includes(file.name.slice(file.name.lastIndexOf(".")))) {
        alertWarning("图片格式不正确");
        return;
      }
      this.imgUrl = URL.createObjectURL(file);
      this.form.img = file;
    },
    changeFirstid() {
      this.form.second_cateid = "";
      this.getSecondIdList();
    },
    getSecondIdList() {
      reqClassifyList({ pid: this.form.first_cateid }).then(res => {
        this.secondClassifyList = res.data.list;
      });
    },
    checked() {
      return new Promise((resolve, reject) => {
        if (this.form.first_cateid === "") {
          alertWarning("一级分类不能为空");
          return;
        }
        if (this.form.second_cateid === "") {
          alertWarning("二级分类不能为空");
          return;
        }
        if (this.form.goodsname === "") {
          alertWarning("商品名称不能为空");
          return;
        }
        if (this.form.price === "") {
          alertWarning("价格不能为空");
          return;
        }
        if (this.form.market_price === "") {
          alertWarning("市场不能为空");
          return;
        }
        if (!this.form.img) {
          alertWarning("图片不能为空");
          return;
        }
        if (this.form.specsid === "") {
          alertWarning("规格不能为空");
          return;
        }
        if (this.form.specsattr.length === 0) {
          alertWarning("请选择商品属性");
          return;
        }

        if (this.form.description === "") {
          alertWarning("描述不能为空");
          return;
        }
        resolve();
      });
    },
    changeSpecs() {
      this.form.specsattr = [];
      this.getArrtsList();
    },
    getArrtsList() {
      let obj = this.spcesList.find(item => item.id === this.form.specsid);
      this.spcesAttrs = obj.attrs;
    },
    add() {
      this.form.description = this.editor.txt.html();
      this.checked().then(() => {
        let obj = {
          ...this.form
        };
        obj.specsattr = JSON.stringify(obj.specsattr);
        let data = new FormData();
        for (let i in obj) {
          data.append(i, obj[i]);
        }
        reqGoodsAdd(data).then(res => {
          if (res.data.code === 200) {
            this.empty();
            this.cancel();
            alertSuccess(res.data.msg);
            this.requsetGoodsList();
            this.requsetGoodsConut();
          }
        });
      });
    },
    update() {
      this.form.description = this.editor.txt.html();
      this.checked().then(() => {
        let obj = {
          ...this.form
        };
        obj.specsattr = JSON.stringify(obj.specsattr);
        let data = new FormData();
        for (let i in obj) {
          data.append(i, obj[i]);
        }
        reqGoodsUpdate(data).then(res => {
          if (res.data.code === 200) {
            this.empty();
            this.cancel();
            alertSuccess(res.data.msg);
            this.requsetGoodsList();
          }
        });
      });
    },
    getOne(id) {
      reqGoodsListOne(id).then(res => {
        if (res.data.code === 200) {
          this.form = res.data.list;
          this.form.id = id;
          this.getSecondIdList();
          this.imgUrl = this.$imgPre + this.form.img;
          this.getArrtsList();
          this.form.specsattr = JSON.parse(this.form.specsattr);
          if (this.editor) {
            this.editor.txt.html(this.form.description);
          }
        }
      });
    },
    cancel() {
      this.info.isshow = false;
    }
  },
  mounted() {
    this.requsetClassifyList();
    this.requsetSpcesList(true);
  }
};
</script>
<style scoped>
.line {
  display: flex;
}
.ipt-box {
  flex: 1;
}
.my-upload {
  width: 100px;
  height: 100px;
  border: 1px dashed #ccc;

  position: relative;
}
.my-upload h3 {
  width: 100%;
  height: 100%;
  text-align: center;
  line-height: 100px;
  color: #666;
}
.my-upload .ipt {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0;
  z-index: 2;
  cursor: pointer;
}
.my-upload img {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
}
</style>


