<template>
  <div>
    <el-table
      :data="list"
      style="width: 100%;margin-bottom: 20px;"
      row-key="id"
      border
      :tree-props="{children: 'children', hasChildren: 'hasChildren'}"
    >
      <el-table-column prop="id" label="商品编号" width="110px"></el-table-column>
      <el-table-column prop="goodsname" label="商品名称" width="110px"></el-table-column>
      <el-table-column label="图片">
        <template slot-scope="scope">
          <img :src="$imgPre+scope.row.img" />
        </template>
      </el-table-column>
      <el-table-column label="是否新品">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.isnew===1">是</el-button>
          <el-button type="danger" v-else>否</el-button>
        </template>
      </el-table-column>
      <el-table-column label="是否热卖">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.ishot===1">是</el-button>
          <el-button type="danger" v-else>否</el-button>
        </template>
      </el-table-column>
      <el-table-column label="状态">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.status===1">启用</el-button>
          <el-button type="info" v-else>禁用</el-button>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="primary" @click="edit(scope.row.id)">编辑</el-button>
          <el-button type="danger" @click="del(scope.row.id)">删 除</el-button>
          <!-- <del-btn @confirm="del(scope.row.id)"></del-btn> -->
        </template>
      </el-table-column>
    </el-table>

    <el-pagination
      layout="prev, pager, next"
      :page-size="size"
      @current-change="changePage"
      :total="total"
    ></el-pagination>
  </div>
</template>
<script>
import { reqGoodsDel } from "../../../util/request";
import { mapActions, mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters({
      list: "goods/list",
      size: "goods/size",
      total: "goods/total"
    })
  },
  components: {},
  data() {
    return {};
  },
  methods: {
    changePage(page) {
      this.changePageAction(page);
    },
    edit(id) {
      this.$emit("edit", id);
    },
    del(id) {
      this.$confirm("此操作将永久删除此条信息, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          reqGoodsDel(id).then(res => {
            if (res.data.code == 200) {
              this.$message({
                type: "success",
                message: res.data.msg
              });
            } else {
              this.$message({
                type: "info",
                message: res.data.msg
              });
            }
            this.requsetGoodsList();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    ...mapActions({
      requsetGoodsList: "goods/requsetGoodsList",
      requsetGoodsConut: "goods/requsetGoodsConut",
      changePageAction: "goods/changePageAction"
    })
  },
  mounted() {
    this.requsetGoodsList();
    this.requsetGoodsConut();
  }
};
</script>
<style scoped>
img {
  width: 100px;
  height: 100px;
}
</style>


